import { Component } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

import { OpenModalService } from '../../../../core/services/open-modal.service';
import { MapManagerService } from '../../../../core/services/map-manager.service';

import { isSmallMobileDevice } from '../../../../shared/utils/responsive.util';
import { Place } from '../../../../core/models/location.model';
import { PLACE_TYPES } from '../../../../main/main.constants';
import { AnalyticsService } from '../../../../core/services/analytics/analytics.service';

interface INotifyUser {
  email: string;
  zip: string;
}

interface IOption {
  key:      string;
  value:    string;
  selected: boolean;
  place?:   Place;
}

interface IModel {
  searchOptions:    Array<IOption>;
  preferredActions: Array<IOption>;
  step:             number;
  hideModal:        boolean;
  mapZoom:          number;
  mapType:          'hybrid'|'styled';
}

interface OnboardingProcessModel {
  notification: string;
}

type SaveSettingsOption = 'start'|'see-details'|'search'|'notify-me';

@Component({
  selector: 'app-onboarding-process',
  templateUrl: './onboarding-process.component.html',
  styleUrls: ['./onboarding-process.component.css'],
})

export class OnboardingProcessComponent
  extends DialogComponent <OnboardingProcessModel, string>
  implements OnboardingProcessModel {

  private isSmallMobileDevice: boolean = isSmallMobileDevice();

  private keyToSaveToLS: string = 'preferredOptions';
  private disableNotifyMeButton: boolean = true;

  private place: Place = {
    placeType: PLACE_TYPES.LOCALITY,
    point: {                   // Cologne is a default place to start
      lat: 50.93784385154836,
      lng: 6.953931101932653,
    },
  };

  public notification: string = '';
  public _name: string = 'onboarding-process';

  public searchControl: FormControl;
  public destinationCount: number = 3;
  public woandersAreChosen: boolean = false;

  public notifyMeForm: FormGroup;
  public user: INotifyUser = null;
  public placeHolderValue: string = 'Suche nach deiner Adresse';

  public model: IModel = {
    searchOptions: [
      {
        key: 'cologne',
        value: 'Kln und Umgebung',
        selected: false,
        place: {
          placeType: PLACE_TYPES.LOCALITY,
          point: {
            lat: 50.93784385154836,
            lng: 6.953931101932653,
          },
        },
      },
      {
        key: 'nuremberg',
        value: 'Nrnberg und Umgebung',
        selected: false,
        place: {
          placeType: PLACE_TYPES.LOCALITY,
          point: {
            lat: 49.44722602965892,
            lng: 11.056038995988501,
          },
        },
      },
      {
        key: 'other',
        value: 'Woanders in Deutschland',
        selected: false,
        place: {
          placeType: PLACE_TYPES.LOCALITY,
          point: {
            lat: 50.93784385154836,   // althought "Somewhere else" was chosen,
            lng: 6.953931101932653,   // Cologne is a default place to start
          },
        },
      },
    ],
    preferredActions: [
      {
        key: 'kaufen',
        value: 'Eine Immobilie kaufen',
        selected: false,
      },
      {
        key: 'sehen',
        value: 'Mein Eigentum sehen',
        selected: false,
      },
      {
        key: 'stoebern',
        value: 'Stbern',
        selected: false,
      },
    ],
    step: 1,
    hideModal: false,
    mapZoom: 19,        // default, small scale
    mapType: 'hybrid',
  };

  public get nextStepIsDisabled(): boolean {
    let array: Array<any> = [];
    let anySelected: Array<any> = [];
    let result: boolean = false;

    if (this.model.step === 1) array = this.model.searchOptions;
    if (this.model.step === 2) array = this.model.preferredActions;

    anySelected = array.filter(item => item.selected);
    result = anySelected.length === 0;

    if (this.model.step === 3) result = false;
    if (this.model.step === 2 && this.woandersAreChosen) result = false;

    return result;
  }

  public get email() {
    return this.notifyMeForm.get('email');
  }

  public get zip() {
    return this.notifyMeForm.get('zip');
  }

  public get disableNotifyingButton(): boolean {
    return this.disableNotifyMeButton;
  }

  private _getPreferredOptionsFromLS(): any {
    try {
      return JSON.parse(localStorage.getItem(this.keyToSaveToLS)) || {};
    } catch (error) {
      return {};
    }
  }

  private _getChosenCity(): any {
    const savedSettings = this._getPreferredOptionsFromLS();
    const place = (savedSettings.searchOptions != null)
                ? savedSettings.searchOptions.filter(item => item.selected)[0]
                : savedSettings.searchOptions.filter(item => item.key === 'other')[0];
    return place;
  }

  private _setChosenCity(city: Place): void {
    const defaultChoise = this.model.searchOptions.filter(item => item.key === 'other')[0];
    this.place = (city != null) ? city : defaultChoise.place;
  }

  private _savePrefferedOptionsInLS(): void {
    localStorage.setItem(this.keyToSaveToLS, JSON.stringify(this.model));
  }

  private _setZoomScaleTo(zoom: 'small'|'large'): void {
    let scale: number;
    let mapType: 'hybrid'|'styled';

    if (zoom === 'small') {
      scale = 19;
      mapType = 'hybrid';
    } else if (zoom === 'large') {
      scale = 12;
      mapType = 'styled';
    }

    this.model.mapZoom = scale;
    this.model.mapType = mapType;
  }

  private _clearItemsStatus(array: any[]): void {
    array.forEach((part, index, theArray) => {
      theArray[index].selected = false;
    });
  }

  private _saveCityMapSettings(): void {
    const chosenLocation: Place = (this.place != null)
                                ? this.place
                                : this._getChosenCity().place;

    this.place = chosenLocation;

    const mapSetting = {
      zoom:       this.model.mapZoom,
      mapTypeId:  this.model.mapType,
      center:     chosenLocation.point,
      activeMarkerId:       '',            // empty marker for start position
      activeMarkerType:     null,
      needOpenDetailWindow: true,
      needMakeMeOwner:      false,
    };

    this.mapManager.saveMapSettings('main', mapSetting);
  }

  private _saveAllSettingsWith(action: 'see-immovables'|'search'): void {
    if (action === 'search')          this._setZoomScaleTo('large');
    if (action === 'see-immovables')  this._setZoomScaleTo('small');

    this._savePrefferedOptionsInLS();
    this._saveCityMapSettings();
  }

  private checkWoandersChosen(): void {
    const place = this.model.searchOptions.filter(item => item.selected)[0];
    if (place.key === 'other') {
      this.destinationCount = 2;
      this.woandersAreChosen = true;
    }
  }

  private _setDefaultUser(): void {
    this.user = {
      email: '',
      zip: '',
    };
  }

  private _initWonadersPageForm(): void {
    this._setDefaultUser();

    this.notifyMeForm = new FormGroup({
      'email': new FormControl(this.user.email, [
        Validators.required,
        Validators.email,
      ]),
      'zip': new FormControl(this.user.zip, [
        Validators.required,
      ]),
    });
  }

  constructor(
    public dialogService: DialogService,
    private openModalService: OpenModalService,
    private mapManager: MapManagerService,
    private analyticsService: AnalyticsService,
    // private http: HttpClient,  // later for sending messages
  ) {
    super(dialogService);

    this.searchControl = new FormControl();
    this._initWonadersPageForm();

    const savedOptions = this._getPreferredOptionsFromLS();
    if (savedOptions['step'] != null) this.model = savedOptions;
  }

  public handleChangedPlace($event: Place): void {
    this.place = $event;
  }

  public moveStepBack(): void {
    if (this.model.step > 1) this.model.step -= 1;
  }

  public moveStepForward(): void {
    if (this.model.step === 1) this.checkWoandersChosen();
    if (this.model.step < 3)   this.model.step += 1;
    else this.goToEstateAnd('see-details');
  }

  public closeButtonClickHandler(): void {
    this.model.hideModal = true;

    this._saveAllSettingsWith('search');
    this.closeModal();
  }

  public closeModal(): void {
    this.openModalService.close();
    super.close();
  }

  public goToEstateAnd(option: SaveSettingsOption): void {
    const zoom = 19;
    this.model.hideModal = true;

    // although there are two scale (19\17), after place change is emitted to mapManager,
    // map-component use 17 scale by default for every jump

    if (option === 'search') {
      this._saveAllSettingsWith('search');
    }

    if (option === 'start') {
      this._saveAllSettingsWith('search');
    }

    if (option === 'see-details') {
      this._saveAllSettingsWith('see-immovables');
    }

    if (option === 'notify-me') {
      this.model.hideModal = true;
      this._saveAllSettingsWith('search');
    }

    this.mapManager.changePlace({place: this.place, zoom, name: ''});
    this.closeModal();
  }

  public onOptionClickHandler(index: number): void {
    this._clearItemsStatus(this.model.searchOptions);
    const currSelection: boolean = this.model.searchOptions[index].selected;
    this.model.searchOptions[index].selected = !currSelection;

    const choise = this.model.searchOptions.filter(item => item.selected)[0];

    this._setChosenCity(choise.place);
    this._saveCityMapSettings();
  }

  public onActionClickHandler(index: number): void {
    const currSelection: boolean = this.model.preferredActions[index].selected;
    this.model.preferredActions[index].selected = !currSelection;
  }

  public checkNotifyFormState(): void {
    this.disableNotifyMeButton = !(this.email.errors == null && this.zip.errors == null);
  }

  public notifyMeClickHandler(): void {
    if (this.disableNotifyMeButton) return;

    /** @TODO:
     * that's for sending-messages implementation, activate later, possible */

    // const data = {
    //   Email: email,
    //   Zip: zip,
    //   Date: new Date()
    // };
    // this._sendNotificationMail(data);

    this.analyticsService.triggerFormSubmissionSuccess('onboardingProcessForm', 'subscribe-to-notifications');
    this.goToEstateAnd('notify-me');
  }

  public jumpToStart(): void {
    this.woandersAreChosen = false;
    this.model.step = 1;
  }
}
