import { Component } from '@angular/core';

import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

import { OpenModalService } from '../../../../core/services/open-modal.service';
import { AuthService } from '../../../../core/services/auth.service';
import { StateManagerService } from '../../../../core/services/state-manager.service';
import { PropertiesRepositoryService} from '../../../../core/services/properties-repository.service';
import { ExtendedMarkerModel } from '../../../../core/models/extended-marker.model';
import { RegistrationComponent } from '../../../registration/pages/registration/registration.component';
import { templateStatusToFrontend } from '../../../../shared/utils/deserializator.util';
import { PrefillAddressService } from '../../../../core/services/prefill-address.service';

export interface InfoModel {
  marker: ExtendedMarkerModel;
}

@Component({
  selector: 'app-appartment-list',
  templateUrl: './apartments-list.component.html',
  styleUrls: ['./apartments-list.component.css']
})

export class ApartmentListComponent
  extends DialogComponent <InfoModel, string>
  implements InfoModel {

  public marker: ExtendedMarkerModel = null;
  public apartment: ExtendedMarkerModel = null;

  public _name: string = 'apartments-modal';

  public salePrice(model: ExtendedMarkerModel): string {
    if (model.SalePrice) return model.SalePrice.toLocaleString('de-DE');
    else return model.Price.toLocaleString('de-DE');
  }

  constructor(
    public dialogService: DialogService,
    public openModalService: OpenModalService,
    public authService: AuthService,
    public stateManagerService: StateManagerService,
    public propertiesRepository: PropertiesRepositoryService,
    private prefillAddressService: PrefillAddressService,
  ) {
    super(dialogService);
  }

  public openRegisterForm(): void {
    const role = 'global';
    const activeTab = this.authService.isUserLoggedIn() ? 'address' : 'registration';
    const mode = activeTab === 'address' ? 'add_property' : 'registration';
    let nextStep = '';

    if (!this.authService.isUserLoggedIn() && activeTab === 'registration') {
      this.stateManagerService.setPropertyIdForNewHolder(this.marker.id);

      nextStep = 'add_property';
    }

    this.dialogService.addDialog(
      RegistrationComponent,
      { role, activeTab, mode, nextStep, needPrefillData: true, }
    )
      .subscribe((message) => {
        this.openModalService.close();
      });
      this.close();
  }

  public extendedSellingStatus(status: string): string {
    return templateStatusToFrontend(status);
  }
}

