import {
  Component,
  Input, Output,
  EventEmitter
} from '@angular/core';

import { MapFilterService } from '../../../../core/services/map-filter.service';
import { PropertiesRepositoryService } from '../../../../core/services/properties-repository.service';

import { ZOOM_CONSTANTS } from '../../../../main/main.constants';

@Component({
  selector: 'app-hint-no-markers',
  templateUrl: './no-markers-hint.component.html',
  styleUrls: ['./no-markers-hint.component.css']
})

export class HintNoMarkersComponent {

  @Input() noMarkersInBound: boolean = false;
  @Input() zoom: number = ZOOM_CONSTANTS.MINIMUM_ZOOM.value;

  @Output() close = new EventEmitter<boolean>();

  public _name: string = 'hint-no-markers';

  public get isMinimumZoomLevel(): boolean {
    return this.zoom === ZOOM_CONSTANTS.MINIMUM_ZOOM.value;
  }

  constructor(
    private propertyRepositoryService: PropertiesRepositoryService,
    public mapFilterService: MapFilterService
   ) { }

  public zoomOut(): void {
    this.propertyRepositoryService.clearCache();
    this.mapFilterService.setZoomOutForResults(true);
  }

  public goToFilter(): void {
    this.mapFilterService.toggleFiltersShown(true);
    this.close.emit(true);
  }
}
