import {
  Component,
  Input,
} from '@angular/core';

import { ZoomLevel } from '../../../../core/models/location.model';
import { SUPPORTED_CITIES } from '../../../../core/models/supported-cities.model';

@Component({
  selector: 'app-map-data-source',
  templateUrl: './map-data-source.component.html',
  styleUrls: [
    './map-data-source.component.css',
  ],
})
export class MapDataSourceComponent {
  @Input()
  city: string;

  @Input()
  zoomLevel: ZoomLevel;

  public _name: string = 'map-data-source';

  public isCitySupported(): boolean {
    return this.isCologneCity || this.isNurembergCity;
  }

  public get isCologneCity(): boolean {
    return this.city === SUPPORTED_CITIES.COLOGNE;
  }

  public get isNurembergCity(): boolean {
    return this.city === SUPPORTED_CITIES.NUREMBERG;
  }

  public get isHighZoom(): boolean {
    return this.zoomLevel === 'HIGH';
  }

  public get isNeighborhoodZoom(): boolean {
    return this.zoomLevel === 'NEIGHBOURHOOD';
  }
}
