import {
  Component,
  OnChanges, OnDestroy,
  Input, Output,
  EventEmitter,
  NgZone,
  ComponentFactoryResolver,
  Injector,
} from '@angular/core';

import { Observable, Subscription } from 'rxjs';

import { PropertiesRepositoryService } from '../../../../core/services/properties-repository.service';
import { StateManagerService } from '../../../../core/services/state-manager.service';
import { AuthService } from '../../../../core/services/auth.service';

import propertyGoogleMapsOverlay from './property-google-maps-overlay';
import { PropertyViewComponent } from './property-view/property-view.component';
import { ExtendedMarkerModel } from '../../../../core/models/extended-marker.model';

@Component({
  selector: 'app-fancy-info-window',
  templateUrl: './fancy-info-window.component.html',
  styleUrls: ['./fancy-info-window.component.css'],
})

export class FancyInfoWindowComponent implements OnChanges, OnDestroy {

  @Input() markerId;
  @Input() needOpenDetailWindow: boolean = false;
  @Input() googleMap: any = null;
  @Input() isShown: boolean;

  @Output() windowClicked: EventEmitter<string> = new EventEmitter<string>();
  @Output() prevMarker: EventEmitter<string> = new EventEmitter<string>();
  @Output() nextMarker: EventEmitter<string> = new EventEmitter<string>();
  @Output() close: EventEmitter<string> = new EventEmitter<string>();
  @Output() openDetails: EventEmitter<string> = new EventEmitter<string>();
  @Output() isOpenChanged = new EventEmitter();

  private _subscriptionMap: Map<string, Subscription> = new Map<string, Subscription>();
  private overlay: any;
  private component: any;

  public marker: ExtendedMarkerModel;

  private async _loadBuilding(marker: ExtendedMarkerModel): Promise<any> {
    if (marker.BuildingPropertyId == null || marker.BuildingPropertyId === `${void 0}`) return;
    marker.building = await this.propertiesRepository.getMarkerDetails(
      marker.BuildingPropertyId,
      !this.authService.isUserLoggedIn()
    );
  }

  private _authChanged(): void {
    if (!this.authService.isUserLoggedIn()) return;
    if (this.markerId == null) {
      this.closeMarkerDetailWindow();
      return;
    }
  }

  private async _fetchMarkerDetails(): Promise<any> {
    if (this.markerId == null) {
      this.closeMarkerDetailWindow();
      return;
    }

    const marker = await this.propertiesRepository.getMarkerDetails(this.markerId, !this.authService.isUserLoggedIn());
    if (!marker.isStatusIdle) this.markersDetailsChanged(marker);
  }

  private closeMarkerDetailWindow(): void {
    this.isOpenChanged.emit(false);
    this.stateManagerService.closeMarkerDetailWindow();
  }

  private _show(): void {
    const factory = this.resolver.resolveComponentFactory(PropertyViewComponent);
    this.component = factory.create(this.injector);
    this.component.instance.marker = this.marker;
    this.component.instance.close.subscribe(() => {
      this._hide();
      this.close.emit();
    });
    // @FIXME: later check this code for further use
    // this.component.instance.nextMarker.subscribe((marker) => {
    //  this.markerId = marker.id;
    //  this._fetchMarkerDetails()
    // });
    this.component.changeDetectorRef.detectChanges();

    this.overlay = propertyGoogleMapsOverlay({
      detectChanges: () => {
        if (this.component  && !this.component.changeDetectorRef['destroyed']) {
          this.component.changeDetectorRef.detectChanges();
        }
      },
      map: this.googleMap,
      html: this.component.location.nativeElement,
      latlng: new google.maps.LatLng(this.marker.Latitude, this.marker.Longitude),
    });

  }

  private _hide(): void {
    if (this.overlay) this.overlay.setMap(null);
    if (this.component) this.component.destroy();
  }

  constructor(
    private propertiesRepository: PropertiesRepositoryService,
    private authService: AuthService,
    private resolver: ComponentFactoryResolver,
    private injector: Injector,
    public stateManagerService: StateManagerService,
    private _zone: NgZone,
  ) {}

  public ngOnChanges(): void {
    this.subscribeOnIfNeed('authChanged', this.authService.events, this._authChanged);
    this._hide();
    this.marker = new ExtendedMarkerModel({});
    this._fetchMarkerDetails();
    if (this.component && !this.component.changeDetectorRef['destroyed']) {
      this.component.changeDetectorRef.detectChanges();
    }
  }

  public ngOnDestroy(...args): void {
    this.unsubscribeAll();
  }

  public unsubscribeAll(): void {
    this._subscriptionMap.forEach((subscription) => subscription.unsubscribe());
  }

  public unsubscribe(key: string): void {
    this._subscriptionMap.get(key).unsubscribe();
  }

  public subscribeOn(key: string, stream: Observable<any>, handler: (data: any) => void): void {
    if (this._subscriptionMap.has(key)) this._subscriptionMap.get(key).unsubscribe();
    this._subscriptionMap.set(key, stream.subscribe(handler.bind(this)));
  }

  public subscribeOnIfNeed(key: string, stream: Observable<any>, handler: (data: any) => void): void {
    if (this._subscriptionMap.has(key)) return;
    this._subscriptionMap.set(key, stream.subscribe(handler.bind(this)));
  }

  public markersDetailsChanged(marker: ExtendedMarkerModel): void {
    this._zone.run(() => {
      this.marker = marker;
      // @FIXME: later check this code for further use
      // this._loadBuilding(marker);
      this._show();
      this.isOpenChanged.emit(true);

      this.stateManagerService.changeActiveMarker(this.marker);
    });
  }

  // @FIXME: later check this code for further use

  // public onInfoWindowClick() {
  //   if (this.markerId) {
  //     this.windowClicked.emit(this.markerId);
  //   }
  // }

  // public prevMarkerHandler(event) {
  //   event.stopPropagation();
  //   this.prevMarker.emit(this.markerId);
  // }
  //
  // public nextMarkerHandler(event) {
  //   event.stopPropagation();
  //   this.nextMarker.emit(this.markerId);
  // }

  // public closeHandler(event) {
  //   event.stopPropagation();
  //   this.overlay.setMap(null);
  //   this.component.destroy();
  //   this.close.emit();
  // }
}
