import { Component, Inject } from '@angular/core';
import { Router } from '@angular/router';

import { DialogService } from 'ng2-bootstrap-modal';
import { NgxModalStack } from 'ngx-multi-modal';

import { AuthService } from '../../../../../core/services/auth.service';
import { UserService } from '../../../../../core/services/user.service';
import { FilterOptions } from '../../../../../core/models/filter-options.model';
import { MapFilterService } from '../../../../../core/services/map-filter.service';
import { propTypeToBackEnd, statusToBackEnd } from '../../../../../shared/utils/deserializator.util';
import { PropertiesRepositoryService } from '../../../../../core/services/properties-repository.service';
import { OpenModalService } from '../../../../../core/services/open-modal.service';
import { isEmptyString } from '../../../../../shared/utils/empty-string.util';
import { isNullOrUndefined } from '../../../../../shared/utils/null-or-undefined.util';
import { isSmallMobileDevice } from '../../../../../shared/utils/responsive.util';
import { SaveSearchAlertNameComponent } from '../../save-search-alert-name/save-search-alert-name.component';
import { LoggerService } from '../../../../../core/services/logger/logger.service';
import { LOGGER } from '../../../../../core/services/logger/logger.provider';
import { SellingStatus, SHARED_ROUTES_NAMES } from '../../../../../app.constants';
import { SignupMobileMenuComponent } from '../../../../registration/components/signup-mobile-menu/signup-mobile-menu.component';

interface SearchRequestSettings {
  name?: string;
  emailFrequency?: string;
  isActive?: boolean;
  city: string;
  priceMin?: number;
  priceMax?: number;
  livingAreaMin?: number;
  livingAreaMax?: number;
  propertyTypes: Array<string>;
  sellingStatuses: Array<string>;
}

@Component({
  selector: 'app-search-request-button',
  templateUrl: './search-request-button.component.html',
  styleUrls: ['./search-request-button.component.css']
})
export class SearchRequestButtonComponent {
  public _name: string = 'search-request-button';
  public searchAlertName: string = '';
  private isSmallMobileDevice: boolean = isSmallMobileDevice();

  public get filterSettings(): FilterOptions {
    return this.mapFilterService.filters;
  }

  public get searchAlertsData(): SearchRequestSettings {
    let sellingStatuses: Array<string> = this.sellingStatusToParams(this.filterSettings.SellingStatus);

    if (this.propertyTypesToParams(this.filterSettings.PropertyType).length === 0) {
      return;
    }

    if (sellingStatuses.length === 0) {
      sellingStatuses = ['make_me_move', 'for_sale', 'active'];
    }

    return {
      name: this.searchAlertName,
      city: 'Nrnberg',
      priceMin: this.filterSettings.PriceFrom,
      priceMax: this.filterSettings.PriceTo,
      livingAreaMin: this.filterSettings.AreaSize,
      livingAreaMax: this.filterSettings.AreaSize2,
      propertyTypes: this.propertyTypesToParams(this.filterSettings.PropertyType),
      sellingStatuses,
    };
  }

  private async createSearchAlert(): Promise<boolean> {
    const data = this.convertSettingsToString(this.searchAlertsData);

    try {
      await this.propertiesRepositoryService.createSearchAlerts(data);
      return true;
    } catch (error) {
      this.logger.error('SearchRequestButtonComponent:createSearchAlert:Error', error);
      return false;
    }
  }

  private convertSettingsToString(settings: SearchRequestSettings): string {
    const data = JSON.stringify(settings);
    return data;
  }

  private sellingStatusToParams(sellingStatus: SellingStatus): Array<string> {
    if (sellingStatus === null || !Object.keys(sellingStatus).length) return [];

    const statuses = Object.keys(sellingStatus);
    const options = statuses
      .filter(option => sellingStatus[option] && option !== 'Idle')
      .map(status => statusToBackEnd(status));

    return options;
  }

  private propertyTypesToParams(propertyType: string): Array<string> {
    if (propertyType === 'PropertyTypeWhereReturnZeroProperties') return [];
    else if (isEmptyString(propertyType)) return ['apartment', 'single_family_house'];
    return [propTypeToBackEnd(propertyType)];
  }

  constructor(
    private authService: AuthService,
    private userService: UserService,
    private mapFilterService: MapFilterService,
    private openModalService: OpenModalService,
    private dialogService: DialogService,
    private router: Router,
    private propertiesRepositoryService: PropertiesRepositoryService,
    private modalService: NgxModalStack,
    @Inject(LOGGER) private logger: LoggerService,
  ) { }
  public async addSearchAlertName(): Promise<boolean> {
    try {
      const modalRef = this.modalService.openFromComponent(SaveSearchAlertNameComponent);
      const name: string = await modalRef.result;
      if (!isEmptyString(name)) {
        this.searchAlertName = name;
        return true;
      } else {
        return false;
      }
    } catch (error) {}
  }

  public async saveSearchAlert(): Promise<void> {
    if (isNullOrUndefined(this.searchAlertsData)) {
      return;
    }

    const addSearchAlertNameResult = await this.addSearchAlertName();
    if (addSearchAlertNameResult) {
      const result = this.createSearchAlert();
      if (result) {
        this.router.navigate([`/${SHARED_ROUTES_NAMES.PROFILE}/${SHARED_ROUTES_NAMES.SEARCH}`]);
      }
    }
  }

  public async openSearchAlertModal(): Promise<void> {
    if (this.authService.isUserLoggedIn()) {
      await this.saveSearchAlert();
    } else {
      const addSearchAlertNameResult = await this.addSearchAlertName();
      if (addSearchAlertNameResult) {
        this.authService.setCreateSearchAlertsSignupInfo(this.searchAlertsData);
        this.showSignup();
      }
    }
  }

  private showSignup(): void {
    if (this.isSmallMobileDevice) {
      this.openSignUpMobileMenu();
    } else {
      this.openSignUpModal();
    }
  }

  private openSignUpModal(): void {
    this.router.navigate([{ outlets: { 'modal': [ 'signup'] }}]);
  }

  private openSignUpMobileMenu(): void {
    this.modalService.openFromComponent(SignupMobileMenuComponent);
  }
}
