import {
  Component,
  OnInit,
  OnDestroy,
  Inject,
} from '@angular/core';
import { DOCUMENT } from '@angular/common';

import { Subscription, Subject } from 'rxjs';
import { SelectItem } from 'ng2-select';

import { StaticStorageService } from '../../../../core/services/static-storage.service';
import { MapManagerService } from '../../../../core/services/map-manager.service';
import { MapFilterService } from '../../../../core/services/map-filter.service';
import { PropertiesRepositoryService } from '../../../../core/services/properties-repository.service';
import { ExtendedMarkerModel } from '../../../../core/models/extended-marker.model';
import { isSmallMobileDevice, isSmallDevice } from '../../../../shared/utils/responsive.util';
import { WINDOW } from '../../../../core/services/window.provider';
import { Coordinates, CoordinatesOptions } from '../../../../core/models/coordinates.model';
import { ZOOM_CONSTANTS } from '../../../../main/main.constants';
import {
  FilterOptions,
  ListingListResult,
  QueryOptions,
  InitialPropertyObject,
  PropertiesListFeatures,
  Geometry,
} from '../../../../core/models/properties-repository.model';
import { LoggerService } from '../../../../core/services/logger/logger.service';
import { LOGGER } from '../../../../core/services/logger/logger.provider';

interface ICarousel {
  topTab: boolean;
  bottomTab: boolean;
}

interface ListingsCount {
  all: number;
  withAddress: number;
  withoutAddress: number;
}

interface ListingsQueryOptions {
  ordering: 'sale_price' | '-sale_price';
  show_address: boolean;
}

export interface GeoListing {
  property: ExtendedMarkerModel;
  geometry: Geometry;
}

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})

export class SidebarComponent implements OnDestroy, OnInit {
  private sortChanged: Subject<void> = new Subject<void>();
  private tabChanged: Subject<void> = new Subject<void>();
  private cashedZoom: number = ZOOM_CONSTANTS.MINIMUM_ZOOM.value;
  private cashedCoordinates: Coordinates;

  public listingsCount: ListingsCount = {
    all: 0,
    withAddress: 0,
    withoutAddress: 0,
  };
  public queryOptions: ListingsQueryOptions = {
    ordering: 'sale_price',
    show_address: true,
  };
  public _name: string = 'sidebar';
  public sortMode: Array<SelectItem> = [];
  public nextMarkersListUrl: string = '';
  public tooltipPosition: string = 'bottom';
  public displayHeight: number = this.document.body.clientHeight - 56;
  public possiblePropertiesCount: number = 0;
  public isSidebarShown: boolean = true;
  public isSortMarkersTimeout: boolean = true;
  public isLoaded: boolean = false;
  public isSmallMobileDevice: boolean = isSmallMobileDevice();
  public isSmallDevice: boolean = isSmallDevice();
  public markersList: ListingListResult = null;
  public nextMarkersList: ListingListResult = null;
  public markers: Array<GeoListing> = [];
  public markersWithoutAddress: Array<GeoListing> = [];
  public subscriptionMap: Map<string, Subscription> = new Map<string, Subscription>();
  public carouselObj: ICarousel = { topTab: true, bottomTab: false };
  public containerHeight: string = `${this.displayHeight - 211}px`;
  public titleWithAddress: string = `${this.listingsCount.withAddress} OBJEKTE MIT ADRESSE`;
  public titleWithoutAddress: string = `${this.listingsCount.withoutAddress} OBJEKTE OHNE ADRESSE`;

  private loadMarkersList(listings: ListingListResult): void {
      this.isLoaded = false;
      this.clearMarkers();

      this.markersList = listings;
      this.nextMarkersListUrl = this.markersList.next;

      this.getResultsMarkersList(this.markersList);
  }

  private async loadMarkersListByUrl(): Promise<void> {
    if (this.nextMarkersListUrl) {
      this.nextMarkersList = await this.propertiesRepository.getListingsByUrl(this.nextMarkersListUrl);
      this.nextMarkersListUrl = this.nextMarkersList.next;

      this.getResultsMarkersList(this.nextMarkersList);
    }
  }

  private counterUpdated(counters: ListingsCount): void {
    this.listingsCount = counters;

    if (counters && counters.all) {
      this.possiblePropertiesCount = counters.all;
      this.titleWithAddress = `${counters.withAddress} OBJEKTE MIT ADRESSE`;
      this.titleWithoutAddress = `${counters.withoutAddress} OBJEKTE OHNE ADRESSE`;
    } else {
      this.possiblePropertiesCount = 0;
    }
  }

  private coordinatesUpdated({ coordinates = null, zoom = null }: CoordinatesOptions): void {
    let _zoom: number = 0;
    let _coordinates: Coordinates = {} as Coordinates;

    if (!zoom && !coordinates) {
      _zoom = this.cashedZoom;
      _coordinates = this.cashedCoordinates;
    } else {
      _zoom = zoom;
      this.cashedZoom = zoom;

      _coordinates = coordinates;
      this.cashedCoordinates = coordinates;
    }

    const options: QueryOptions = {
      filterOptions : this.mapFilterService.filters as FilterOptions,
      coordinates: _coordinates,
      zoom: _zoom
    };

    const params: string = this.generateParamsString(options);
    this.subscribeOn('listings', this.propertiesRepository.getListings(params), this.loadMarkersList);
    this.subscribeOn('markersCount', this.propertiesRepository.getListingsCount(options), this.counterUpdated);
  }

  private clearMarkers(): void {
    this.markers.length = 0;
    this.markersWithoutAddress.length = 0;
  }

  private renderMarkerData(markersList: Array<PropertiesListFeatures>): void {
    markersList.forEach(marker =>  {
      if (this.mapManagerService.hasFavoriteId(marker.properties.uuid)) marker.properties.isFavorite = true;
      else marker.properties.isFavorite = false;

      if (this.queryOptions.show_address) {
        this.markers.push({
          geometry: marker.geometry,
          property: new ExtendedMarkerModel(marker.properties),
        });
      } else {
        this.markersWithoutAddress.push({
          geometry: marker.geometry,
          property: new ExtendedMarkerModel(marker.properties),
        });
      }
    });

    this.isLoaded = true;
  }

  private filtersUpdated(): void {
    /**
     * This is bad way but it necessary solution for current architecture
     * Next step: change architecture of queries and delete this
     * bad part of code.
     * @FIXME: delete it after refactoring.
    */
    if (this.sidebarActive) {
      this.collectDataForLoadMarkers();
      this.isSidebarShown = true;
    } else {
      this.isSidebarShown = false;
      this.listingsCount.all = 0;
      this.listingsCount.withAddress = 0;
      this.listingsCount.withoutAddress = 0;
      this.unsubscribeOne('listings');
      this.unsubscribeOne('markersCount');
      this.unsubscribeOne('coordinatesUpdated');
      this.markers.length = 0;
      this.markersWithoutAddress.length = 0;
      this.isLoaded = false;
    }
  }

  private collectDataForLoadMarkers(): void {
    this.subscribeOn('coordinatesUpdated', this.mapFilterService.coordinates$, this.coordinatesUpdated);
  }

  private generateParamsString(options: QueryOptions): string {
    const mainOptions = this.propertiesRepository.convertOptions(options);
    return `${mainOptions}&ordering=${this.queryOptions.ordering},-has_url&show_address=${this.queryOptions.show_address}`;
  }

  private sortTypeChanged(): void {
    this.coordinatesUpdated({coordinates: null, zoom: null});
  }

  private subscribeOn(key: string, stream: any, handler: (data: any) => void): void {
    if (this.subscriptionMap.has(key)) this.subscriptionMap.get(key).unsubscribe();
    this.subscriptionMap.set(key, stream.subscribe(handler.bind(this)));
  }

  private unsubscribeAll(): void {
    this.subscriptionMap.forEach(subscription => subscription.unsubscribe());
  }

  private unsubscribeOne(name: string): void {
    if (this.subscriptionMap.has(name)) this.subscriptionMap.get(name).unsubscribe();
  }

  private getResultsMarkersList(markers: ListingListResult): void {
    if (markers && markers.features instanceof Array) this.renderMarkerData(markers.features);
  }

  private async getFavoriteData(): Promise<void> {
    let favoriteData: Array<string> = [];

    try {
      favoriteData = await this.propertiesRepository.getFavoritesForListings();
    } catch (error) {
      this.logger.error('SidebarComponent::getFavoriteData', error);
    } finally {
      this.updateFavoriteData(favoriteData);
    }
  }

  private updateFavoriteData(favoriteIds: Array<string>): void {
    favoriteIds.forEach(id => this.mapManagerService.addFavoriteId(id));
  }

  private checkDisplayHeight(): void {
    this.displayHeight = this.window.innerHeight - 56;
    this.containerHeight = `${this.displayHeight - 211}px`;
  }

  constructor(
    private mapManagerService: MapManagerService,
    private propertiesRepository: PropertiesRepositoryService,
    public mapFilterService: MapFilterService,
    public staticStorage: StaticStorageService,
    @Inject(LOGGER) private logger: LoggerService,
    @Inject(WINDOW) private window: Window,
    @Inject(DOCUMENT) private document: Document,
  ) {
    this.checkDisplayHeight = this.checkDisplayHeight.bind(this);
  }

  public async ngOnInit(): Promise<void> {
    await this.getFavoriteData();

    this.subscribeOn('filtersUpdated', this.mapManagerService.getFilterOptionsStream(), this.filtersUpdated);
    this.subscribeOn('sortTypeChanged', this.sortChanged, this.sortTypeChanged);
    this.subscribeOn('addressTypeChanged', this.tabChanged, this.sortTypeChanged);

    if (this.document.body.clientHeight === 0) this.checkDisplayHeight();
    if (this.isSmallMobileDevice) this.isSidebarShown = false;

    this.window.addEventListener('resize', this.checkDisplayHeight);
  }

  public ngOnDestroy(): void {
    this.unsubscribeAll();
    this.window.removeEventListener('resize', this.checkDisplayHeight);
  }

  public closeSidebar() {
    this.toggleSidebar();
  }

  public async onScroll(): Promise<void> {
    await this.loadMarkersListByUrl();
  }

  public toggle(state: boolean, tab: string): void {
    this.carouselObj[tab] = state;
    if (state) {
      this.queryOptions.show_address = tab === 'bottomTab' ? false : true;
      this.tabChanged.next(null);
    }
  }

  public toggleSidebar(): void {
    if (!this.sidebarActive) {
      this.isSidebarShown = false;
      return;
    }

    this.isSidebarShown = !this.isSidebarShown;
    if (this.isSidebarShown) {
      this.carouselObj.topTab = true;
    } else {
      this.unsubscribeOne('listings');
    }
  }

  public setSortMode(value: SelectItem): void {
    this.sortMode.push(value);
    if (value.text === 'Kaufpreis (hchste zuerst)') this.queryOptions.ordering = '-sale_price';
    else this.queryOptions.ordering = 'sale_price';
    this.sortChanged.next(null);
  }

  public compareEstimatedPriceInc(propertyA: ExtendedMarkerModel, propertyB: ExtendedMarkerModel): number {
    return propertyA.EstimatedPrice - propertyB.EstimatedPrice;
  }

  public compareEstimatedPriceDec(propertyA: ExtendedMarkerModel, propertyB: ExtendedMarkerModel): number {
    return propertyB.EstimatedPrice - propertyA.EstimatedPrice;
  }

  public trackByFn(index: number, item: ExtendedMarkerModel): string {
    return item.id;
  }

  public get sidebarActive(): boolean {
    const { ForSale, MakeMeMove } = this.mapFilterService.filters.SellingStatus;
    return (ForSale || (!ForSale && !MakeMeMove));
  }
}
