export const SUPPORTED_CITIES = {
  NUREMBERG: 'Nrnberg',
  COLOGNE: 'Kln',
};

export function getSupportedCity(formattedAddress: string): string {
  const supportedCities = Object.values(SUPPORTED_CITIES);
  for (const supportedCity of supportedCities) {
    const isCitySupported = formattedAddress.includes(supportedCity);
    if (isCitySupported) {
      return supportedCity;
    }
  }

  return '';
}
